<template>
   <!--   -->
  <div class="login">
            <div class="welcome"><img src="/static/images/welcome.png"></div>
                <div class="login-form">
                    <div class="login-inp"><label></label><input type="text" placeholder="" v-model="username"/></div>
                    <div class="login-inp"><label></label><input type="password" placeholder="" v-model="password"/></div>
                    <div class="login-inp"><a href="#" @click="login()">{{btnName}}</a></div>
                </div>
            <div class="login-txt"><a href="javaScript:;" @click="redirects('/register')"></a>|<a href="#" @click="tip()"></a>
            </div>
        <div style="text-align:center;">
        </div>
  </div>

</template>

<script>
// import { mapState,mapActions,mapGetters } from 'vuex';
import { Toast } from 'vant';
// import { Dialog } from'vant';
import axios from 'axios';
export default {
  name: 'login',
  components:{
  },
  data() {
    return {
       username:null,
       password:null,
       btnName:'',
    }
  },
  computed: {
  },
  methods: {
    //
    rules(v,choose){
        if(choose == 'user') {
             let username = v.trim();
             let uPattern = /^[a-zA-Z0-9_]{4,12}$/g;

             if(username.split('').length > 12 ) { return false;}

             if(!uPattern.test(username)) {
                 Toast('416(,,)')
             }
            //  console.log(username,v.trim());
        }else if(choose == 'pass') {
            let password = v.trim();
            //61111
            let pPattern = /^.*(?=.{6,})(?=.*\d)(?=.*[A-Z])(?=.*[a-z])(?=.*[!@#$%^&*? ]).*$/;
             if(!pPattern.test(password)) {
                 Toast('o()o');
             }else{
                 Toast(' ^_^');
             }
        } 
    },
    login(){
        if(this.username == null || this.password == null ) {
            Toast(' o()o')
        }else{
            let user = this.username;
            let password = this.password;
            axios({
                url:'/api/login',
                method:'post',
                data:{
                    user,
                    password
                }
            }).then((res) => {
                if(res.data.err == 0){
                    this.$router.push('/me');
                }else{
                    Toast(res.data.err);
                }                
            })
        }
    },
    redirects(url) {
      this.$router.push(url);
        if(this.$route.name == 'login') {
            this.btnName = '';
        }else if (this.$route.name == 'register') {
            this.btnName = '';
        }
    },
    tip() {
        Toast('');
    },
  },
  watch: {
  },
  directives: {
  },
  beforeCreate() {

  },
  created() { 
  }
}
</script>

<style lang="less" scoped>
  @import url('../assets/css/login.less');
</style>